/* src/custom.css */

/* Fix for Autocomplete as before */
.pac-container {
  z-index: 10000 !important;
}

/* --- NEW: FORCE DRAWING TOOLS VISIBLE --- */

/* Google uses .gmnoprint for the toolbar container */
.gmnoprint, .gm-draw {
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
}

/* This ensures the toolbar sits on top of the Bootstrap Modal */
.gm-style-mtc + .gmnoprint, 
div[style*="z-index: 1000001"] {
    z-index: 1000001 !important; 
}

/* Hide only the 'Map/Satellite' buttons to keep it clean */
.gm-style-mtc {
    display: none !important;
}


